import { isoDate } from '@speedlo/graphql'
import React from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { DateTime } from '../../../graph/generated'

interface IProps {
  autoFilter?: string
}

export interface IStatsOrdersFilter {
  branches: ReadonlyArray<ID>
  drivers: ReadonlyArray<ID>
  dateTimeFrom: DateTime
  dateTimeTo: DateTime
}

const context = React.createContext<IStatsOrdersFilter | undefined>(undefined)

export const useOrderStatisticsFilter = () => React.useContext(context)

export const OrdersStatisticsLoader: React.FC<IProps> = ({
  children,
  autoFilter,
}) => {
  const { statistics } = useRoot()
  const [filter, setFilter] = React.useState<IStatsOrdersFilter | undefined>()

  const executeFilter = () => {
    setFilter({
      branches: [...statistics.branchesFilter],
      drivers: [...statistics.drivers],
      dateTimeFrom: isoDate(statistics.dateTimeFrom),
      dateTimeTo: isoDate(statistics.dateTimeTo),
    })
  }

  React.useEffect(
    () => {
      if (autoFilter) {
        executeFilter()
      }
    },
    [autoFilter],
  )

  const defaultValues = {
    dateTimeFrom: statistics.dateTimeFrom,
    dateTimeTo: statistics.dateTimeTo,
  }

  return (
    <Form
      onSubmit={executeFilter}
      onChange={({ values }) => statistics.updateFilter(values)}
      defaultValues={defaultValues}
    >
      <context.Provider value={filter}>{children}</context.Provider>
    </Form>
  )
}
